* {
  background-color: #333;
  color: #fff;
}

.title {
  margin: 50px;
  font-size: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.skills-icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 100px;
  margin: 20px;
  margin-bottom: 30px;
  transition: transform 0.2s ease-out;
}

.logo:hover {
  transform: scale(1.1) translateY(-14px);
}

.hover-effect {
  position: relative;
  display: inline-block;

}

.text {
  visibility: hidden;
  width: 100%;
  position: absolute;
  bottom: -30px;
  left: 0;
  text-align: center;
  background-color: #333;
  color: #fff;
  padding: 5px 0;
  transform: translateY(-30px);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.hover-effect:hover .text {
  visibility: visible;
  opacity: 1;
}
